.advantages {
  position: relative;
  z-index: 1;

  width: 100%;
  background-color: #e3ecf6;
  background-image: url("../img/advantages-img-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/advantages-img-desktop@2x.jpg");
  }

  &::after {
    position: absolute;
    content: "";
    z-index: 1;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;

    background: rgba(#e3ecf6, 0.9);
  }
}

.advantages__inner {
  padding-top: 110px;
  padding-bottom: 80px;
}

.advantages__wrapper {
  position: relative;
  z-index: 2;

  display: flex;
  flex-direction: column;
  align-items: center;
}

.advantages__wrapper h2 {
  @include title-desktop;
  @include reset;

  margin-bottom: 49px;
}

.advantages__list {
  @include list-reset;
  display: flex;
  flex-wrap: wrap;

  max-width: 100%;
}

.advantages__item {
  position: relative;

  width: 31%;

  padding-top: 81px;
  padding-right: 30px;
  padding-bottom: 26px;
  padding-left: 30px;
  margin-right: 3.4%;
  margin-bottom: 40px;

  background-color: $second-color;

  &:nth-child(3n) {
    margin-right: 0;
  }
}

.advantages__item svg {
  position: absolute;
}

.advantages__item--ship svg {
  top: 30px;
  left: 31px;

  width: 27px;
  height: 32px;
}

.advantages__item--ticks svg {
  top: 35px;
  left: 30px;

  width: 38px;
  height: 21px;
}

.advantages__item--boat svg {
  top: 38px;
  left: 29px;

  width: 42px;
  height: 15px;
}

.advantages__item h3 {
  @include reset;
  @include text(18px, 1.5, 500);

  margin-bottom: 9px;
}

.advantages__item p {
  @include reset;
  @include text(14px, 1.4, 400);
}

@media (max-width: $tablet-width-max) {
  .advantages {
    background-image: url("../img/advantages-img-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-tablet@2x.jpg");
    }
  }

  .advantages__inner {
    padding-top: 91px;
    padding-bottom: 60px;
  }

  .advantages__wrapper h2 {
    @include title-tablet;

    margin-bottom: 51px;
  }

  .advantages__item {
    width: 30.3%;
    padding-top: 70px;
    padding-right: 15px;
    padding-bottom: 23px;
    padding-left: 20px;
    margin-right: 4.4%;
  }

  .advantages__item--ship svg {
    top: 25px;
    left: 20px;

    width: 21px;
    height: 26px;
  }

  .advantages__item--ticks svg {
    top: 30px;
    left: 20px;

    width: 33px;
    height: 19px;
  }

  .advantages__item--boat svg {
    top: 32px;
    left: 20px;

    width: 38px;
    height: 13px;
  }

  .advantages__item h3 {
    @include text(18px, 1.4, 500);

    margin-bottom: 10px;
  }
}

@media (max-width: $mobile-width-max) {
  .advantages {
    background-image: url("../img/advantages-img-mobile@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/advantages-img-mobile@2x.jpg");
    }
  }

  .advantages__inner {
    padding-top: 58px;
    padding-bottom: 36px;
  }

  .advantages__wrapper h2 {
    @include title-mobile;

    margin-bottom: 32px;
  }

  .advantages__list {
    flex-direction: column;
  }

  .advantages__item {
    width: 100%;
    padding-top: 70px;
    padding-right: 15px;
    padding-bottom: 21px;
    padding-left: 20px;
    margin-bottom: 30px;
  }

  .advantages__item--ticks svg {
    top: 29px;
  }

  .advantages__item--boat svg {
    top: 32px;
    left: 20px;
  }

  .advantages__item h3 {
    @include text(18px, 1.4, 500);

    width: 67%;
    margin-bottom: 10px;
  }
}
